import { defineComponent } from "vue";
import IconPlus from "../helpers/IconPlus.vue";
import Colors from "../types/ColorsEnum";
export default defineComponent({
    components: {
        IconPlus,
    },
    data() {
        return {
            accordions: [
                {
                    title: "1. Чем отличаются стальная и чугунная печи?",
                    content: "Вес чугуна значительно выше, чем у стали, а цена — больше. Соответственно, установка и транспортировка такой печи будут значительно труднее.",
                    color: Colors.Green,
                },
                {
                    title: "2. Где ещё можно купить печку?",
                    content: "Вы можете заказать нашу буржуйку не только в интернет-магазине, но и на других популярных платформах (wildberries, ozon, yandex и т. д) или купить её в сети наших розничных магазинов (в Москве или других городах&nbsp;России).",
                    color: Colors.Green,
                },
                {
                    title: "3. У печки снимаются ножки — детали не приварены друг к другу. Устойчива ли такая конструкция?",
                    content: "Печка устойчива и долговечна. Её устройство подразумевает, что ножки идеально стыкуются с корпусом и&nbsp;не&nbsp;соскальзывают.",
                    color: Colors.Green,
                },
                {
                    title: "4. Как ухаживать за буржуйкой?",
                    content: "Перед чисткой нужно удалить из печи оставшуюся после растопки золу с помощью совка. Поверхность буржуйки металлическая, поэтому её можно мыть мягкой влажной тряпкой или губкой. Не рекомендуется чистка изделия абразивными материалами, острыми и твёрдыми предметами.",
                    color: Colors.Green,
                },
                {
                    title: "5. Идут ли в комплект конвекторные пластины?",
                    content: "Нет, не идут.",
                    color: Colors.Green,
                },
            ],
        };
    },
    methods: {
        createAnimation(element) {
            let menu = element.querySelector(".accordion-menu");
            let box = element.querySelector(".accordion-content");
            this.gsap.set(box, { height: "auto" });
            let animation = this.gsap
                .from(box, {
                height: 0,
                duration: 0.5,
                ease: "power1.inOut",
            })
                .reverse();
            return function (clickedMenu) {
                if (clickedMenu === menu) {
                    animation.reversed(!animation.reversed());
                }
                else {
                    animation.reverse();
                }
            };
        },
        changeColor(i) {
            this.accordions.forEach((element, index) => {
                index == i
                    ? (element.color =
                        element.color == Colors.Red ? Colors.Green : Colors.Red)
                    : (element.color = Colors.Green);
            });
        },
    },
    mounted() {
        this.$nextTick(function () {
            let groups = this.gsap.utils.toArray(".accordion-item");
            let menus = this.gsap.utils.toArray(".accordion-menu");
            let menuToggles = groups.map(this.createAnimation);
            menus.forEach((menu, i) => {
                menu.addEventListener("click", () => {
                    toggleMenu(menu);
                    this.changeColor(i);
                });
            });
            function toggleMenu(clickedMenu) {
                menuToggles.forEach((toggleFn) => toggleFn(clickedMenu));
            }
        });
    },
});
